import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardTitle,
  CCardHeader,
  CCardBody,
  CCardText,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableRow,
  CButtonGroup,
  CButton,
  // CListGroup,
  // CListGroupItem
} from "@coreui/react";
import { useParams, useHistory } from "react-router-dom";

import Loader from "../../components/Loader";
import Message from "../../components/Message";
import { Authentification } from "../../components/index";

import {
  getRestaurants,
  deleteRestaurant,
} from "../../provider/restaurantFetch";

const DetailRestaurant = () => {
  /**Params Route */
  let { id } = useParams();
  let history = useHistory();

  /** State */
  const [restaurant, setRestaurant] = useState(null);
  const [error, setError] = useState(null);

  /** Run once, when the component mounts */
  useEffect(() => {
    console.log("Run once, when the component mounts");

    async function fetch() {
      const resp = await getRestaurants(id);

      resp?.status ? setRestaurant(resp?.data) : setError(resp?.error);
    }

    fetch();
  }, [id]);

  const back = () => {
    history.push(`/restaurants`);
  };

  const deleteFunction = async (id) => {
    const resp = await deleteRestaurant(id);

    if (resp.status) {
      history.push(`/restaurants`);
    }
  };

  if (restaurant === null) {
    return <Loader />;
  }

  if (error) {
    return <Message> {error}</Message>;
  }
  return (
    <>
      <Authentification />
      <CRow>
        <CCol xs={12}>
          <CCard className="mb-4">
            <CCardHeader>
              <CRow>
                <CCol sm={5}>
                  <strong>restaurant {id}</strong>
                </CCol>
                <CCol sm={7}>
                  <CButtonGroup className="float-end me-3">
                    <CButton
                      color="outline-secondary"
                      className="mx-1"
                      onClick={() => back()}
                    >
                      Retour
                    </CButton>
                    <CButton
                      color="outline-danger"
                      className="mx-0"
                      onClick={() => deleteFunction(restaurant?._id)}
                    >
                      Suppprimer
                    </CButton>
                  </CButtonGroup>
                </CCol>
              </CRow>
            </CCardHeader>
            <CCardBody>
              <CTable>
                <CTableBody>
                  <CTableRow>
                    <CTableDataCell>Name</CTableDataCell>
                    <CTableDataCell>{restaurant.name}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Description</CTableDataCell>
                    <CTableDataCell>{restaurant.description}</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Commercant</CTableDataCell>
                    <CTableDataCell>
                      {restaurant.user.firstName} {restaurant.user.lastName}
                    </CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableDataCell>Creation</CTableDataCell>
                    <CTableDataCell>{restaurant.createdAt}</CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
      {restaurant?.categories.map((c, indexCategory) => (
        <div key={c._id} id={c.name} className="container my-4">
          <h2> {c.name} </h2>
          <CRow>
            {c?.foods.map((f, indexFood) => (
              <CCol key={f._id} md={6} xs={12} className={`my-1`}>
                <CCard className="hover">
                  <CCardBody>
                    <CRow>
                      <CCol md={6}>
                        <CCardTitle>{f.name}</CCardTitle>
                        <CCardText>{f.descrption}</CCardText>

                        {f.priceDrop ? (
                          <>
                            <s>
                              <CCardText as="h5">${f.price}</CCardText>
                            </s>
                            <CCardText as="h3">${f.priceDrop}</CCardText>
                          </>
                        ) : (
                          <CCardText as="h3">${f.price}</CCardText>
                        )}
                        <CCardText as="h3">
                          {f.isPopular && "Popular"}
                        </CCardText>
                        <CCardText as="h3">
                          {f.available && "Available"}
                        </CCardText>

                        {/* {f?.optionLists.map((o) => (
                          <div key={o._id}>
                            <CCardTitle>{o.name}</CCardTitle>
                            <CCardText>{o.type}</CCardText>
                            <CListGroup variant="flush">
                              {o?.options.map((opt) => (
                                <CListGroupItem key={opt._id}>
                                  {opt.name} - ${" "}
                                  {opt.priceDrop ? opt.priceDrop : opt.price}
                                </CListGroupItem>
                              ))}
                            </CListGroup>
                          </div>
                        ))} */}
                      </CCol>
                      <CCol xs={6}>
                        <img
                          alt={`${f.name}`}
                          src="img/mcDo_1.PNG"
                          rounded
                          height={150}
                          width={150}
                        />
                      </CCol>
                    </CRow>
                  </CCardBody>
                </CCard>
              </CCol>
            ))}
          </CRow>
        </div>
      ))}
    </>
  );
};

export default DetailRestaurant;
